import Container from "../../common/Container";
import Header from "../../common/Header";
import Section from "../../common/Section";
import { StyledStrong, StyledText } from "./styled";

export default () => (
    <Container>
        <Header title="O autorze" />
        <Section
            title="Adrian Potka"
            body={
                <StyledText> <StyledStrong>Cze wszystkim</StyledStrong> <br />
                    ycie poprowadzio mnie tutaj krtymi drogami, ale to, e tu jestem nie jest
                    przypadkowe. Zawsze, krcia mnie praca na komputerze i zawsze lubiem
                    spdza czas przy nim. Ju w wieku gimnazjamnym zastanawiaem si czy nie
                    pj do technikum uczy si programowania. To wtedy wanie po raz pierwszy
                    odrzuciem t myl i poszedem do liceum o profilu matematycznym. Tam szo
                    mi cakiem niele i dlatego zoyem papiery na politechnik na kierunku
                    Budownictwo. Na szczcie, albo na nieszczcie nie dostaem si. Moim koem
                    zapasowym by kierunek Kryminologia. I taki to wanie kierunek skoczyem i
                    obroniem magistra. Przez chwil nawet mylaem, e pjd pracowa jako policjant,
                    ale w momencie, gdy nie zdaem testw prsychologicznych uwiadomiem sobie, e
                    nie pasuj do policji. Znajomi mwi, e jestem za miy . Od tego czasu wiedziaem,
                    e chciabym zaczc uczy si programowania. Czekaem jedynie na moment w, ktrym
                    ustatkuj si w yciu i bd mie wicej czasu. I oto jestem  Rok po lubie,
                    a za miesic bd ojcem, wic stwierdziem, e jest to dobry moment, aby zacz
                    si uczy. Bardzo chciabym mie wicej czasu da swojej rodziny i te troche
                    lepiej zarabia, aby nie martwi si co bdzi w przyszoi <br />
                    To jak ciesz si uczc uwiadamia, e jest to dobry kierunek w yciu i bardzo
                    mocny motywator, aby si nie poddawa.
                </StyledText>
            }
        />
    </Container>
);